import { gql } from 'apollo-boost'

export const GET_PRODUCT = gql`
  query($pName: String) {
    productItems(where: { category: { name: $pName } }) {
      id
      name
      price
      brand
      unitType
      description
      MenuDetail
    }
  }
`

export const GET_PRODUCT_DETAIL = gql`
  query($id: ID) {
    productItem(where: { id: $id }) {
      id
      name
      price
      brand
      unitType
      description
      amount
      MenuDetail
      category {
        name
      }
    }
  }
`

export const ADD_PRODUCT_TO_CART = gql`
  mutation($pid: ID, $amount: Number, $shid: ID,amount: $Int) {
    updateShoppingCart(
      where: { id: $$shid }
      data: {
        productItems: {
          upsert: {
            where: { key: $shid+$pid }
            create: {
              key: "ck6gfsmop00i4073170gno0ykck6g0tu2s007a0731toft9idy"
              product: { connect: { id: "ck6g0tu2s007a0731toft9idy" } }
              amount: 100
            }
            update: {
              product: { connect: { id: "ck6g0tu2s007a0731toft9idy" } }
              amount: 110
            }
          }
        }
      }
    ) {
      id
      productItems {
        product {
          name
        }
        amount
      }
    }
  }
`
